.MainDiv {
    border-bottom: 1px solid black;
    background-color: white;
    width: auto;
    height: 280px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    top:0;
    /*position: sticky;*/
    /*z-index:999;*/
}

.Logo {
    width: 100%;
    height: 100%;
    max-height: 180px;
    object-fit: scale-down;
}

.Menu {
    margin-top: 20px;
    align-self: center;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-column-gap: 90px;
}

.MenuItem {
    font-size: 20px;
    text-decoration: none;
    color: black;
}