.ButtonBar {
    height:100px;
    /*background-color:skyblue;*/
    display: grid;
    grid-template-columns: repeat(4,1fr);
    justify-content: center;
    justify-items: center;
    margin-top:50px;
    margin-bottom:70px;
}

img {
    width:160px;
    height:100%;
    object-fit: scale-down;
}

@media screen and (max-width:700px) {
    img {
        width:110px;
    }

    .ButtonBar {
        margin-top:40px;
        margin-bottom: 0;
    }
}