.Slider {
    background-color: rgb(255,150,150);
    width: auto;
}

.Slide {
    background-color: blue;
    width: 100%;
    display: flex;
}

.SlideImage {
    width: 115%;
    height: auto;
    object-fit: cover;
}

.Slide1Mask {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.5);
    top:0;
    left:0;
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
    flex-direction: column;
}

.Slide1Header {
    color: white;
    text-align: center;
}

.Slide1Subtitle {
    color: white;
    text-align: center;
    width: 80%;
}

.Slide2Text {
    color:black;
    position: fixed;
    top: 35%;
    left: 15%;
    font-size:40px;
}

.Slide2Button {
    background-color: black;
    color: white;
    height: auto;
    width: auto;
    position: fixed;
    top: 55%;
    left: 15%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 15px;
    font-size: 20px;
}

.BestSellers {
    width: auto;
    height: auto;
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.BestSellerHeader  {
    text-align: center;
}

.BestSellerHeaderBar {
    width: 80px;
    height: 5px;
    background-color: black;
    align-self: center;
}

.VisitUsContainer {
    display:flex;
    flex-direction: row;
    width:100%;
    height:auto;
}

.ContactFormContainer {
    /*width:100%;*/
    height:100%;
    /*background-color:skyblue;*/
    padding-left:30px;
    padding-right:30px;
}

.ContactForm {
    display: flex;
    flex-direction: column;
}

.input-label {
    font-size: 20px;
}

.input-text {
    margin-top:10px;
    margin-bottom:10px;
    height:30px;
    border: 4px solid black;
}

.submit-button {
    margin-top:20px;
    height:auto;
    border: 4px solid black;
    background-color: black;
    color:white;
    font-size:30px;
    font-weight: bold;
    padding:10px;
}

.AddressContainer {
    /*width:100%;*/
    height:100%;
    /*background-color:pink;*/
    padding-left:30px;
    padding-right:30px;
}

.Map {
    background-color: bisque;
    width:100%;
    height:300px;
}

@media screen and (max-width: 900px) {
    .Slide2Text {
        top: 30%;
        font-size:35px;
    }
}

@media screen and (max-width: 700px) {
    .VisitUsContainer {
        flex-direction: column;
    }

    .Slide2Text {
        top: 21%;
        font-size:30px;
        width:300px;
    }
}

@media screen and (min-width: 700px) {
    .VisitUsContainer {
        flex-direction: row;
    }
    .AddressContainer {
        width:100%;
    }
    .ContactFormContainer {
        width:100%;
    }
    .ContactForm {
        padding-top:20px;
    }
}
